@import url(https://fonts.googleapis.com/css2?family=Anton&family=Inter&display=swap);
body {
  margin: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    --bg-dark : #23232D;
    --bg-light : #eee;
    --text-dark : #222;
    --text-light : #eee;
    --text-light-secondary : #ccc;
}

.main,
.about {
    height: 100vh;
}
.about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -5% !important;
    width: 80%;
    height: 100vh;
    margin: auto;
}

.about h3 {
    color: var(--textdark);
    font-weight: normal;
    width: 60%;
    font-size: 120%;
    margin-bottom: 2%;
}

.about h1 {
    margin-top: 5%;
    margin-bottom: 2%;
}

.education {
    display: flex;
    justify-content: space-between;
    width: 80%;
}

.education-container {
    width: 33%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    background: #eee8;
    border-radius: 5px;
}

.education-container > h2 + h3 {
    width: 90%;
    padding: 0 5%;
}

.education-container h2 {
    padding: 0 10%;
}

@media (max-width : 1000px){
    .about {
        height: auto;
        padding: 20px;
    }
    .about h3 {
        width: 90%;
    }
    .education {
        display: block;
    }
    .education-container {
        width: 90%;
        margin: 3% auto;
    }
}
.main {
    /* background: url(https://i.ytimg.com/vi/XbzAuRWDN_o/maxresdefault.jpg) no-repeat center center;
    background-size: cover;
    background-blend-mode: multiply;
    background-color: #111; */
    background: linear-gradient(#222, #111);
    color: var(--text-light);
}

.main,
.main-content {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.main-content {
    flex-direction: column;
    margin: auto;
    height: 74%;
}

.main .logo img {
    margin-top: -50px;
    width: 60px;
    -webkit-filter: drop-shadow(0 0 10px #eee5);
            filter: drop-shadow(0 0 10px #eee5);
}

.main h3 + h1 {
    margin: 0;
}

.main h3 {
    color: var(--text-light-secondary);
}

.main .text {
    text-align: center;
}
.skills {
    height: 70vh;
    background-color: #111;
    color: var(--text-light);
    padding: 15vh 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills h1 {
    margin-bottom: 100px;
}

.skills-item {
    display: flex;
    align-items: center;
    width: 60%;
    margin: auto;
    transition: .3s linear;
}

.skills-item h2 {
    margin: 20px;
    width: 200px;
    margin-right: 50px;
}

.progerss-bar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}

.progerss-bar .profress-after {
    position: absolute;
    margin-top: 40px;
    max-width: 42%;
    content:' ';
    background: #2E9AFD;
    height: 3px;
    animation: progress-bar 1s ease-out;
}

@keyframes progress-bar {
    0% {
        width: 0%;
    }
}

.progerss-item {
    cursor: pointer;
}

.progerss-item::before {
    display: block;
    content: '';
    border-radius: 50%;
    position: absolute;
    margin-top:25px;
    margin-left: 20px;
    border: 1px solid #eee;
    background: #222;
    width: 8px;
    height: 8px;
    z-index: 1;
}

.progerss-item:hover::before {
    background: #fff !important;
}

.progerss-item:first-child {
    width: 25px;
}


.progerss-bar .done::before {
    background: #298fee;
}

@media (max-width : 1000px) {
    .skills {
        height: auto;
    }
    .skills-item {
        display: block;
    }
}
.contact {
    height: 170px;
    padding: 40px;
    background: var(--bg-light);
    color : var(--text-dark);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-items {
    display: flex;
    justify-content: space-between;
    width: 220px;
}

.contact-items .icons {
    cursor: pointer;
}

.contact-items .icons img {
    width: 40px;
    pointer-events: none;
    -webkit-filter: opacity(90%);
            filter: opacity(90%);
}

.contact-items .icons:hover img {
    -webkit-filter: opacity(100%);
            filter: opacity(100%);
}
.works {
    height: 90vh;
    padding: 5vh;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.works h1 {
    font-weight: bold;
    margin-bottom: 50px;
}

.works-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 80%;
}

.work-item {
    flex: 1 1;
    height: 100%;
    /* border: 1px solid #ddd; */
    background-color: #eee8;
    border-radius: 5px;
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.work-item .img-container {
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.work-item .img-container img {
    padding: 10px;
    max-width: 100%;
    min-width: 40%;
    object-fit: cover;
}

.work-item .view a {
    color: var(--text-dark);
    font-weight: 600;
    text-decoration: none;
    background-color: #ddd;
    padding: 10px 30px;
    border-radius: 10px;
    border: 1px solid #eee;
    transition: .3s linear;
}

.work-item .view a:hover {
    background-color: var(--bg-ligth);
    border: 1px solid #222;
}

@media (max-width : 900px) {
    .works-container {
        display: block;
    }
    .work-item,
    .works {
        height: auto;
    }
}
