.skills {
    height: 70vh;
    background-color: #111;
    color: var(--text-light);
    padding: 15vh 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills h1 {
    margin-bottom: 100px;
}

.skills-item {
    display: flex;
    align-items: center;
    width: 60%;
    margin: auto;
    transition: .3s linear;
}

.skills-item h2 {
    margin: 20px;
    width: 200px;
    margin-right: 50px;
}

.progerss-bar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}

.progerss-bar .profress-after {
    position: absolute;
    margin-top: 40px;
    max-width: 42%;
    content:' ';
    background: #2E9AFD;
    height: 3px;
    animation: progress-bar 1s ease-out;
}

@keyframes progress-bar {
    0% {
        width: 0%;
    }
}

.progerss-item {
    cursor: pointer;
}

.progerss-item::before {
    display: block;
    content: '';
    border-radius: 50%;
    position: absolute;
    margin-top:25px;
    margin-left: 20px;
    border: 1px solid #eee;
    background: #222;
    width: 8px;
    height: 8px;
    z-index: 1;
}

.progerss-item:hover::before {
    background: #fff !important;
}

.progerss-item:first-child {
    width: 25px;
}


.progerss-bar .done::before {
    background: #298fee;
}

@media (max-width : 1000px) {
    .skills {
        height: auto;
    }
    .skills-item {
        display: block;
    }
}