.about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -5% !important;
    width: 80%;
    height: 100vh;
    margin: auto;
}

.about h3 {
    color: var(--textdark);
    font-weight: normal;
    width: 60%;
    font-size: 120%;
    margin-bottom: 2%;
}

.about h1 {
    margin-top: 5%;
    margin-bottom: 2%;
}

.education {
    display: flex;
    justify-content: space-between;
    width: 80%;
}

.education-container {
    width: 33%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    background: #eee8;
    border-radius: 5px;
}

.education-container > h2 + h3 {
    width: 90%;
    padding: 0 5%;
}

.education-container h2 {
    padding: 0 10%;
}

@media (max-width : 1000px){
    .about {
        height: auto;
        padding: 20px;
    }
    .about h3 {
        width: 90%;
    }
    .education {
        display: block;
    }
    .education-container {
        width: 90%;
        margin: 3% auto;
    }
}