.works {
    height: 90vh;
    padding: 5vh;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.works h1 {
    font-weight: bold;
    margin-bottom: 50px;
}

.works-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 80%;
}

.work-item {
    flex: 1 1;
    height: 100%;
    /* border: 1px solid #ddd; */
    background-color: #eee8;
    border-radius: 5px;
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.work-item .img-container {
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.work-item .img-container img {
    padding: 10px;
    max-width: 100%;
    min-width: 40%;
    object-fit: cover;
}

.work-item .view a {
    color: var(--text-dark);
    font-weight: 600;
    text-decoration: none;
    background-color: #ddd;
    padding: 10px 30px;
    border-radius: 10px;
    border: 1px solid #eee;
    transition: .3s linear;
}

.work-item .view a:hover {
    background-color: var(--bg-ligth);
    border: 1px solid #222;
}

@media (max-width : 900px) {
    .works-container {
        display: block;
    }
    .work-item,
    .works {
        height: auto;
    }
}