    background: linear-gradient(#222, #111);
    color: var(--text-light);
}

.main,
.main-content {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.main-content {
    flex-direction: column;
    margin: auto;
    height: 74%;
}

.main .logo img {
    margin-top: -50px;
    width: 60px;
    filter: drop-shadow(0 0 10px #eee5);
}

.main h3 + h1 {
    margin: 0;
}

.main h3 {
    color: var(--text-light-secondary);
}

.main .text {
    text-align: center;
}