.contact {
    height: 170px;
    padding: 40px;
    background: var(--bg-light);
    color : var(--text-dark);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-items {
    display: flex;
    justify-content: space-between;
    width: 220px;
}

.contact-items .icons {
    cursor: pointer;
}

.contact-items .icons img {
    width: 40px;
    pointer-events: none;
    filter: opacity(90%);
}

.contact-items .icons:hover img {
    filter: opacity(100%);
}